<template>
    <div class="container">
        <NavBar title="红包锦囊" />
        <Banner
            :bg="banner.bg"
            :deeplink="banner.deeplink"
        />
        <div
            v-for="(item, index) in tip"
            :key="`tip-${index}`"
            class="tip">
            <div class="tip-title">
                <i class="tip-title-label" />
                {{ item.title }}
            </div>
            <p
                class="tip-p"
                :key="`tip-p-${index}`"
                v-for="(p, index) in item.p"
            >
                {{ p }}
            </p>
        </div>
        <Footer
            :button="nav.button"
            :text="nav.p"
        />
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getConfigByKey } from '@/api/common';
import Banner from './Banner';
import Footer from './Footer';

export default {
    name: 'Guide',
    components: {
        NavBar,
        Banner,
        Footer,
    },
    data() {
        return {
            banner: {
                bg: '',
                deeplink: '',
            },
            tip: [],
            nav: {
                button: {
                    value: '',
                    deeplink: '',
                },
                p: '',
            },
        };
    },
    created() {
        getConfigByKey({
            key: 'fe-config_chat-guide',
        }).then((res) => {
            console.log(res);
            this.banner = res.data.content.banner;
            this.nav = res.data.content.nav;
            this.tip = res.data.content.tip;

            this.$root.hidePlaceholder();
            this.$o2o({
                e_c: '群红包',
                e_a: '曝光',
                e_n: '红包玩法页面曝光',
            });
        }).catch(() => {
            this.$error();
        });
    },
};
</script>

