<template>
    <div
        class="banner"
        @click="goDeeplink()"
        :style="{backgroundImage: `url(${bg})`}"
    />
</template>

<script>
export default {
    name: 'GuideBanner',
    props: {
        bg: {
            type: String,
            required: true,
        },
        deeplink: {
            type: String,
            required: true,
        },
    },
    methods: {
        goDeeplink() {
            if (this.deeplink) {
                location.href = this.deeplink;
            }
        },
    },
};
</script>

<style scoped>
.banner {
    height: 87px;
    width: 100%;
    background-size: 100% 100%;
}
</style>
